    import {uniCalendar} from "@/components/uni-calendar/uni-calendar"
    import uniPopup from "@/components/uni-popup/uni-popup.vue"
    var graceChecker = require("../../js_sdk/graceui-dataChecker/graceChecker.js")
	export default {
        components: {
            uniCalendar,
            uniPopup
        },
		data() {
            const currentDate = this.getDate({
                format: true
            })
			return {
                nowtime: '', // 今天日期(年-月-日)
                cyear: '', // 年
                cmonth: '', // 月
                cdate: '', // 日
                dateList: {}, // 总日期金额数组
                yearIncomeData: 0, // 月收入数据
                yearExpendData: 0, // 月支出数据
                incomeData: 0, // 本日收入数据
                expendData: 0, // 本日支出数据
                nowList: [], // 今天的数据
                date: currentDate
			}
		},
		onLoad() {
            let date = new Date
            let Y = date.getFullYear()
            let M = date.getMonth() + 1
            let D = date.getDate();
            let nowtime = Y + '-' + M + '-' + D
            this.nowtime = String(nowtime)
            this.cyear = Y
            this.cmonth = M
            this.cdate = D 
        },
        computed: {
            startDate() {
                console.log(this.getDate('start'), '11111')
            },
            endDate() {
                console.log(this.getDate('end'), '22222')
                return this.getDate('end');
            }
        },
		methods: {
            // 点击日期触发
			change(e) {
                console.log(e);
            },
            // 更换月份触发
            monthSwitch(e) {
                console.log(e)
            },
            onForm(e) {
                this.$refs.popup.open()
            },
            bindDateChange: function(e) {
                console.log(e.target.value, '1123123')
                this.date = e.target.value
            },
            // 表单确认
            formSubmit(e) {
                let rule = [
                    {name:"picker", checkType: "notnull", checkRule:"",  errorMsg:"请选择日期"},
                    {name:"radio", checkType: "in", checkRule:"radio1,radio2",  errorMsg:"请选择方式"},
                    {name:"input", checkType: "therge",  errorMsg:"请输入金额"}
                ];
                let formData = e.detail.value;
                let checkRes = graceChecker.check(formData, rule);
                if(checkRes){
                    let time = formData.picker // 日期
                    let cyear = time.split('-')[0] // 年份
                    let cmonth = time.split('-')[1] // 月份
                    let cdate = time.split('-')[2] // 几号
                    let list = {...this.dateList} // 总数据
                    let aaaa = {
                        [cyear]: {
                            [cmonth]: {
                                [cdate]: [formData]
                            }
                        }
                    }
                    let qqq = {}
                    if (JSON.stringify(list) === '{}') {
                        qqq = {
                           [cyear]: {
                               [cmonth]: {
                                   [cdate]: [formData]
                               }
                           }
                        }
                        list = {...qqq}
                    } else {
                        if (list[cyear]) {
                            if (list[cyear][cmonth]) {
                                if (list[cyear][cmonth][cdate]) {
                                    list[cyear][cmonth][cdate].push(formData)
                                } else {
                                    qqq = {
                                        [cdate]: [formData]
                                    }
                                    Object.assign(list[cyear][cmonth], qqq)
                                }
                            } else {
                                qqq = {
                                    [cmonth]: {
                                        [cdate]: [formData]
                                    }
                                }
                                 Object.assign(list[cyear], qqq)
                            }
                        } else {    
                           qqq = {
                               [cyear]: {
                                   [cmonth]: {
                                       [cdate]: [formData]
                                   }
                               }
                           }
                           Object.assign(list, qqq)
                        }
                    }
                    this.dateList = {...list}
                    // uni.showToast({title:"验证通过!", icon:"none"});
                    this.addCalculate(list, cyear, cmonth, cdate) // 计算总金额
                    // this.monthCalculate(list, cyear, cmonth) // 计算月金额
                    // this.todayCalculate(list, cyear, cmonth, cdate) // 计算本日金额
                } else {
                    uni.showToast({ title: graceChecker.error, icon: "none" });
                }
            },
            // 表单取消
            formReset(e) {
                this.$refs.popup.close()
            },
            // 计算总支出金额和总收入金额
            addCalculate(list, cyear, cmonth, cdate) {
                console.log(list, 'list')
                console.log(cyear, 'cyear')
                console.log(cmonth, 'cmonth')
                console.log(cdate, 'cdate')
                let aaa = {
                    '2019': {
                        '11': {
                            '01': [
                                {
                                    input: "123",
                                    picker: "2019-11-01",
                                    radio: "radio1",
                                    ramarks: ""
                                },
                                {
                                    input: "123",
                                    picker: "2019-11-01",
                                    radio: "radio1",
                                    ramarks: ""
                                }
                            ],
                            '02': [
                                {
                                    input: "123",
                                    picker: "2019-11-02",
                                    radio: "radio1",
                                    ramarks: ""
                                }
                            ]
                        }
                    },
                    '2020': {
                        '01': {
                            '02': [
                                {
                                    input: "123",
                                    picker: "2020-01-02",
                                    radio: "radio1",
                                    ramarks: ""
                                }
                            ]
                        },
                        '02': {
                            '11': [
                                {
                                    input: "123",
                                    picker: "2020-02-11",
                                    radio: "radio1",
                                    ramarks: ""
                                }
                            ]
                        }
                    }
                }
                console.log(aaa, 'aaa')
                console.log(aaa.length, 'length')
                let ddd = Object.values(aaa)
                console.log(ddd, 'ddd')
                // let sss = Object.keys(sss)
                // console.log(sss, 'sss')
            },
            // 计算月支出金额和月收入金额
            monthCalculate(list, cyear, cmonth) {
                let yearlistData = list[cyear][cmonth] //月的数据
                let yearIncome = 0 //月收入
                let yearExpend = 0 //月支出
                console.log(yearlistData, '111')
                let aaa = {
                    '2019': {
                        '11': {
                            '01': [
                                {
                                    input: "0.1",
                                    picker: "2019-01-24",
                                    radio: "radio1",
                                    ramarks: ""
                                },
                                {
                                    input: "123.54",
                                    picker: "2019-01-23",
                                    radio: "radio1",
                                    ramarks: ""
                                }
                            ],
                            '02': [
                                {
                                    input: "0.2",
                                    picker: "2019-02-24",
                                    radio: "radio1",
                                    ramarks: ""
                                }
                            ]
                        }
                    }
                }
                let bbb = aaa[cyear][cmonth]
                console.log(bbb, 'qweqwe')
                let ccc = Object.values(bbb)
                let kkk = [] // x
                ccc.map(item =>{
                    console.log(item, 'item')
                    kkk = kkk.concat(item)
                })
                console.log(kkk, 'kkk')
                kkk.map(item => {
                    if (item.radio === "radio1") {
                        yearIncome = (Number(yearIncome)*100 + Number(item.input)*100) /100
                    } else {
                        yearExpend += (Number(yearExpend)*100 + Number(item.input)*100) /100
                    }
                })
                this.yearIncomeData = yearIncome //月收入
                this.yearExpendData = yearExpend //月支出

            },
            // 计算本日支出金额和本日收入金额
            todayCalculate(list, cyear, cmonth, cdate) {
                let listData = list[cyear][cmonth][cdate] //本日的数据
                let income = 0 //本日收入
                let expend = 0 //本日支出
                listData.map(item => {
                    if (item.radio === "radio1") {
                        income = (Number(income)*100 + Number(item.input)*100) /100
                    } else {
                        expend = (Number(expend)*100 + Number(item.input)*100) /100
                    }
                })
                this.incomeData = income
                this.expendData = expend
                this.nowList = listData
            },
            getDate(type) {
                const date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth() + 1;
                let day = date.getDate();
                if (type === 'start') {
                    year = year - 60;
                } else if (type === 'end') {
                    year = year;
                }
                month = month > 9 ? month : '0' + month;;
                day = day > 9 ? day : '0' + day;
                return `${year}-${month}-${day}`;
            }
		}
	}
